/* src/components/Home.css */
.home-container {
    padding: 20px;
    background-color: #1a1a1a;
    color: #fff;
    text-align: center;
}

body {
    font-family: 'EFCO Brookshire', sans-serif;
  }

.hero-section {
    position: relative;
    text-align: center;
    color: white;
    margin-bottom: 20px; /* Add some margin to separate sections */
}

.hero-image {
    width: 100%;
    max-height: 400px;
    object-fit: cover;
    opacity: 0.8;
}

.hero-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: rgba(0, 0, 0, 0.7); /* Darken background for better readability */
    padding: 20px;
    border-radius: 10px;
    width: 80%; /* Ensure text container doesn't overflow the image */
    max-width: 600px; /* Limit the maximum width */
}

.content-section {
    margin-top: 40px;
}

.content-section h2 {
    margin-bottom: 20px;
}

.content-section p, .content-section ul {
    margin-bottom: 20px;
    font-size: 18px;
}

.content-section ul {
    list-style-type: none;
    padding: 0;
}

.content-section ul li {
    background: #333;
    padding: 10px;
    margin: 5px 0;
    border-radius: 5px;
}

/* Media queries for responsive design */
@media (max-width: 768px) {
    .hero-text {
        width: 90%;
        padding: 15px;
    }

    .content-section p, .content-section ul {
        font-size: 16px;
    }
}

@media (max-width: 480px) {
    .hero-text {
        width: 95%;
        padding: 10px;
    }

    .content-section p, .content-section ul {
        font-size: 14px;
    }
}