/* src/components/About.css */
.about-container {
    padding: 40px 20px;
    background-color: #1a1a1a;
    color: #fff;
    text-align: center;
}

.about-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 800px;
    margin: auto;
}

.about-photo {
    width: 100%;
    max-width: 600px;
    height: auto;
    margin-bottom: 20px;
    border-radius: 10px;
}

.about-text {
    text-align: left;
}

.about-text p, .about-text ul {
    margin-bottom: 20px;
    font-size: 18px;
}

.about-text ul {
    list-style-type: none;
    padding: 0;
}

.about-text ul li {
    background: #333;
    padding: 10px;
    margin: 5px 0;
    border-radius: 5px;
}

.spotify-embed {
    margin-top: 40px;
    max-width: 800px;
    width: 100%;
    margin: auto;
}

/* Media queries for responsive design */
@media (max-width: 768px) {
    .about-text p, .about-text ul {
        font-size: 16px;
    }
}

@media (max-width: 480px) {
    .about-text p, .about-text ul {
        font-size: 14px;
    }
}