/* src/components/NavBar.css */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: rgba(0, 0, 0, 0.8);
    padding: 10px 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    position: relative;
}

.navbar-logo img {
    height: 50px;
}

.navbar-toggle {
    display: none;
    font-size: 28px;
    cursor: pointer;
    color: white;
}

.navbar-links {
    list-style: none;
    display: flex;
    gap: 20px;
    margin: 0;
}

.navbar-links li a {
    color: #fff;
    text-decoration: none;
    font-weight: bold;
    padding: 10px;
}

.navbar-links li a:hover {
    color: #ccc;
}

/* Media queries for responsive design */
@media (max-width: 768px) {
    .navbar-toggle {
        display: block;
    }

    .navbar-links {
        display: none;
        flex-direction: column;
        position: absolute;
        top: 60px;
        left: 0;
        width: 100%;
        background: rgba(0, 0, 0, 0.9);
        padding: 10px 0;
        box-shadow: 0 8px 16px rgba(0, 0, 0, 0.9);
    }

    .navbar-links.open {
        display: flex;
    }

    .navbar-links li {
        margin: 10px 0;
    }

    .navbar-links li a {
        padding: 10px 20px;
    }
}
