/* src/components/OrderForm.css */
.order-form {
    max-width: 500px;
    margin: 20px auto;
    padding: 20px;
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.order-form h1 {
    text-align: center;
    margin-bottom: 20px;
}

.order-form label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
}

.order-form input,
.order-form select {
    width: calc(100% - 20px);
    padding: 10px;
    margin: 5px auto 15px auto;
    border: none;
    border-radius: 5px;
    display: block;
    box-sizing: border-box;
}

.order-form button {
    width: 100%;
    padding: 10px;
    background: #444;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.order-form button:hover {
    background: #666;
}

.order-form span {
    color: red;
    font-size: 12px;
    display: block;
    margin-top: -10px;
    margin-bottom: 10px;
}

/* Media queries for responsive design */
@media (max-width: 768px) {
    .about-text p, .about-text ul {
        font-size: 16px;
    }
}

@media (max-width: 480px) {
    .about-text p, .about-text ul {
        font-size: 14px;
    }
}